// =====================================================
// PRODUCTS PAGE
// =====================================================

import { Suspense } from 'react';
import { Metadata } from 'next';
import { ProductGrid } from '@/components/products';
import { PageLoading } from '@/components/ui';
import { createClient } from '@/lib/supabase/server';

export const metadata: Metadata = {
  title: 'Mahsulotlar',
  description: 'Veluna Market - Barcha mahsulotlar. Elektronika, kiyim-kechak, sport va boshqa tovarlar.',
};

interface ProductsPageProps {
  searchParams: { 
    category?: string;
    search?: string;
    sort?: string;
    page?: string;
  };
}

async function getProducts(searchParams: ProductsPageProps['searchParams']) {
  const supabase = await createClient();
  
  let query = supabase
    .from('products')
    .select('*, category:categories(*)', { count: 'exact' })
    .eq('is_active', true);

  // Category filter
  if (searchParams.category) {
    const { data: category } = await supabase
      .from('categories')
      .select('id')
      .eq('slug', searchParams.category)
      .single();
    
    if (category) {
      query = query.eq('category_id', category.id);
    }
  }

  // Search filter
  if (searchParams.search) {
    query = query.ilike('name', `%${searchParams.search}%`);
  }

  // Sorting
  switch (searchParams.sort) {
    case 'price-asc':
      query = query.order('price', { ascending: true });
      break;
    case 'price-desc':
      query = query.order('price', { ascending: false });
      break;
    case 'newest':
      query = query.order('created_at', { ascending: false });
      break;
    case 'popular':
      query = query.order('reviews_count', { ascending: false });
      break;
    default:
      query = query.order('created_at', { ascending: false });
  }

  // Pagination
  const page = parseInt(searchParams.page || '1');
  const limit = 12;
  const from = (page - 1) * limit;
  query = query.range(from, from + limit - 1);

  const { data: products, count } = await query;

  return { products: products || [], count: count || 0, page, limit };
}

async function getCategories() {
  const supabase = await createClient();
  
  const { data } = await supabase
    .from('categories')
    .select('*')
    .eq('is_active', true)
    .order('sort_order', { ascending: true });

  return data || [];
}

export default async function ProductsPage({ searchParams }: ProductsPageProps) {
  const [{ products, count, page, limit }, categories] = await Promise.all([
    getProducts(searchParams),
    getCategories(),
  ]);

  const totalPages = Math.ceil(count / limit);

  return (
    <div className="min-h-screen py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">
            {searchParams.category
              ? categories.find((c: any) => c.slug === searchParams.category)?.name || 'Mahsulotlar'
              : searchParams.search
              ? `"${searchParams.search}" bo'yicha natijalar`
              : 'Barcha mahsulotlar'}
          </h1>
          <p className="text-gray-500">{count} ta mahsulot topildi</p>
        </div>

        <div className="flex flex-col lg:flex-row gap-8">
          {/* Sidebar - Filters */}
          <aside className="w-full lg:w-64 flex-shrink-0">
            <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-4 sticky top-24">
              {/* Categories */}
              <div className="mb-6">
                <h3 className="font-semibold mb-3">Kategoriyalar</h3>
                <div className="space-y-2">
                  <a
                    href="/products"
                    className={`block px-3 py-2 rounded-lg text-sm transition-colors ${
                      !searchParams.category
                        ? 'bg-primary-100 text-primary-700 dark:bg-primary-900/30 dark:text-primary-400'
                        : 'hover:bg-gray-100 dark:hover:bg-gray-700'
                    }`}
                  >
                    Barchasi
                  </a>
                  {categories.map((category: any) => (
                    <a
                      key={category.id}
                      href={`/products?category=${category.slug}`}
                      className={`block px-3 py-2 rounded-lg text-sm transition-colors ${
                        searchParams.category === category.slug
                          ? 'bg-primary-100 text-primary-700 dark:bg-primary-900/30 dark:text-primary-400'
                          : 'hover:bg-gray-100 dark:hover:bg-gray-700'
                      }`}
                    >
                      {category.name}
                    </a>
                  ))}
                </div>
              </div>

              {/* Sort */}
              <div>
                <h3 className="font-semibold mb-3">Saralash</h3>
                <select
                  defaultValue={searchParams.sort || 'newest'}
                  onChange={(e) => {
                    const url = new URL(window.location.href);
                    url.searchParams.set('sort', e.target.value);
                    window.location.href = url.toString();
                  }}
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-800"
                >
                  <option value="newest">Eng yangi</option>
                  <option value="price-asc">Arzon avval</option>
                  <option value="price-desc">Qimmat avval</option>
                  <option value="popular">Mashhur</option>
                </select>
              </div>
            </div>
          </aside>

          {/* Main Content */}
          <main className="flex-1">
            <Suspense fallback={<PageLoading />}>
              <ProductGrid products={products} />
            </Suspense>

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="flex justify-center gap-2 mt-8">
                {Array.from({ length: totalPages }, (_, i) => i + 1).map((p) => (
                  <a
                    key={p}
                    href={`/products?${new URLSearchParams({
                      ...searchParams,
                      page: p.toString(),
                    }).toString()}`}
                    className={`w-10 h-10 flex items-center justify-center rounded-lg transition-colors ${
                      p === page
                        ? 'bg-primary-600 text-white'
                        : 'bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-gray-700'
                    }`}
                  >
                    {p}
                  </a>
                ))}
              </div>
            )}
          </main>
        </div>
      </div>
    </div>
  );
}
